<template>
  <div id="collection-create-project-dialog-component">
    <el-dialog
      title="Create Project"
      v-model="visible"
      :width="step === 1 ? '80%' : '40%'"
      top="45px"
    >
      <!--Step 1-->
      <div class="step-1-container" v-if="step === 1">
        <div class="input-item">
          <p class="label">Project Name:</p>
          <el-input v-model="form.name" clearable> </el-input>
        </div>
        <el-row :gutter="60" v-if="detail">
          <el-col
            v-for="item in detail.collection_fabrics"
            :key="item.fabric.id"
            :span="6"
          >
            <FabricCard :data="item.fabric"></FabricCard>
          </el-col>
        </el-row>
      </div>

      <!--Step 2-->
      <div class="step-2-container" v-if="step === 2">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Expected Finished Time</p>
            <el-date-picker
              v-model="form.time"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            >
            </el-date-picker>
          </el-col>
          <el-col :span="24">
            <p class="label">Members</p>
            <el-select v-model="form.members">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Brands</p>
            <el-select v-model="form.brands">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="step === 1" type="info" @click="visible = false">
            Cancel
          </el-button>
          <el-button v-if="step === 2" type="info" @click="step = 1">
            Back
          </el-button>
          <el-button v-if="step === 1" type="success" @click="step = 2">
            Next
          </el-button>
          <el-button v-if="step === 2" type="success" @click="onDoneClick">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCard from "@/components/FabricCard.vue";
import { GetCollection, PostCollectionDuplicate } from "@/typings/store";
import { ElNotification } from "element-plus";
import { OptionItem } from "@/typings/asideFilter";
import { useRouter } from "vue-router";

export const options: OptionItem[] = [
  { label: "Jerry", value: "Spring", checked: false },
  { label: "Angel", value: "Summer", checked: false },
  { label: "Jason", value: "Fall", checked: false },
  { label: "Nike-Chloe", value: "Winter", checked: false },
  { label: "Nike-Sam", value: "Winter", checked: false }
];

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCard
  },
  setup() {
    const store = useStore();
    const router = useRouter();

    const visible = ref(false);
    const id = ref(-1);
    const form = reactive({
      fabrics: [],
      name: "",
      time: "",
      members: [],
      brands: []
    });
    const step = ref(1);

    const detail = computed(() => store.state.collection.detail);

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);
    };

    const open = async (_id: number) => {
      step.value = 1;
      id.value = _id;
      visible.value = true;
      await init(_id);
    };

    const onDoneClick = async () => {
      try {
        await router.push({ name: "ProjectDetail", params: { id: 1 } });
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${form.name} Failed.`
        });
      }
      visible.value = false;
    };

    return {
      form,
      detail,
      visible,
      step,
      options,

      open,
      onDoneClick
    };
  }
});

export default Component;
</script>

