
import { defineComponent, ref, computed, watch } from "vue";
import { useStore } from "vuex";

import FilterTag from "@/components/FilterTag.vue";
import { OptionItem } from "@/typings/asideFilter";
import { GetAvailalbeMembers, GetJoinedMembers } from "@/typings/store";
import {
  AvailableMember,
  CollectionUpdateFabricPriceParams,
  JoinedMember
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { copyToClipboard } from "@/utils";

const Component = defineComponent({
  name: "ShareDialog",
  components: {
    FilterTag
  },
  setup() {
    const store = useStore();
    const collectionId = ref(0);
    const visible = ref(false);
    const selectValue = ref("");

    const tags = computed(() => {
      return [
        { label: "Jerry", value: 1 },
        { label: "Angel", value: 2 },
        { label: "Jason", value: 3 },
        { label: "Nike", value: 4 }
      ];
    });
    const options = computed<OptionItem>(() => {
      return store.state.collection.availableMembers.map(
        (item: AvailableMember) => ({
          label: item.username,
          value: item.member_id,
          checked: false
        })
      );
    });

    watch(selectValue, async id => {
      // if (!id || typeof id !== "number") return;
      try {
        // const params: CollectionUpdateFabricPriceParams = {
        //   collection_id: id,
        //   fabric_id: collectionId.value,
        //   price: fabricPrice.value
        // };
        // const data = await store.dispatch(PostCollectionCreateFabric, params);
        // store.commit(SetFabricRelatedCollections, [
        //   ...store.state.fabric.relatedCollections,
        //   data
        // ]);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
      selectValue.value = "";
    });

    const init = async (id: number) => {
      await store.dispatch(GetAvailalbeMembers, { id, q: "" });
      await store.dispatch(GetJoinedMembers, id);
    };

    const open = async (id: number) => {
      collectionId.value = id;
      visible.value = true;
      await init(id);
    };

    const onTagCloseClick = async (id: number) => {
      try {
        // const params: CollectionUpdateFabricPriceParams = {
        //   collection_id: id,
        //   fabric_id: collectionId.value
        // };
        // await store.dispatch(DeleteCollectionFabric, params);
        // const formatData = store.state.collection.joined_member.filter(
        //   (item: JoinedMember) => item.id !== id
        // );
        // store.commit(SetFabricRelatedCollections, formatData);
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: e
        });
      }
    };

    const onCopy = () => {
      const href = window.location.href;
      copyToClipboard(href);
      ElNotification({
        title: "Success",
        type: "success",
        message: `Your Copied Link ${href}`
      });
    };

    return {
      tags,
      options,
      visible,
      selectValue,

      open,
      onTagCloseClick,
      onCopy
    };
  }
});

export default Component;
