
import { defineComponent, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCard from "@/components/FabricCard.vue";
import { GetCollection, PostCollectionDuplicate } from "@/typings/store";
import { ElNotification } from "element-plus";
import { OptionItem } from "@/typings/asideFilter";
import { useRouter } from "vue-router";

export const options: OptionItem[] = [
  { label: "Jerry", value: "Spring", checked: false },
  { label: "Angel", value: "Summer", checked: false },
  { label: "Jason", value: "Fall", checked: false },
  { label: "Nike-Chloe", value: "Winter", checked: false },
  { label: "Nike-Sam", value: "Winter", checked: false }
];

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCard
  },
  setup() {
    const store = useStore();
    const router = useRouter();

    const visible = ref(false);
    const id = ref(-1);
    const form = reactive({
      fabrics: [],
      name: "",
      time: "",
      members: [],
      brands: []
    });
    const step = ref(1);

    const detail = computed(() => store.state.collection.detail);

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);
    };

    const open = async (_id: number) => {
      step.value = 1;
      id.value = _id;
      visible.value = true;
      await init(_id);
    };

    const onDoneClick = async () => {
      try {
        await router.push({ name: "ProjectDetail", params: { id: 1 } });
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${form.name} Failed.`
        });
      }
      visible.value = false;
    };

    return {
      form,
      detail,
      visible,
      step,
      options,

      open,
      onDoneClick
    };
  }
});

export default Component;
